"use client"

import { useState, useRef, useEffect } from "react"

interface CookingInterfaceProps {
  recipe: {
    recipe_name: string
    ingredients: string[]
    instructions: string[]
  }
  onBack: () => void
}

export function CookingInterface({ recipe, onBack }: CookingInterfaceProps) {
  const [activeTab, setActiveTab] = useState<"ingredients" | "directions">("ingredients")
  const [checkedIngredients, setCheckedIngredients] = useState<Record<number, boolean>>({})
  const [currentStepIndex, setCurrentStepIndex] = useState(0)
  const [timerActive, setTimerActive] = useState<number | null>(null)
  const [timerSeconds, setTimerSeconds] = useState(0)
  const directionsContainerRef = useRef<HTMLDivElement>(null)
  const stepRefsRef = useRef<(HTMLDivElement | null)[]>([])

  // Extract timer from instruction text (e.g., "10 minutes" -> 600 seconds)
  const extractTimer = (text: string): number | null => {
    const timeMatch = text.match(/(\d+)\s*(?:minutes?|mins?|hours?|hrs?)/i)
    if (timeMatch) {
      const value = Number.parseInt(timeMatch[1])
      const unit = timeMatch[0].toLowerCase()
      if (unit.includes("hour")) return value * 3600
      if (unit.includes("minute") || unit.includes("min")) return value * 60
    }
    return null
  }

  // Format seconds to MM:SS
  const formatTime = (seconds: number): string => {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins.toString().padStart(2, "0")}:${secs.toString().padStart(2, "0")}`
  }

  // Highlight time in instruction text
  const highlightTimer = (text: string) => {
    const timeRegex = /(\d+)\s*(?:minutes?|mins?)/gi
    const parts = text.split(timeRegex)

    return parts.map((part, idx) => {
      if (idx % 2 === 1 || parts[idx + 1]?.match(/^s?/i)) {
        // This is a number followed by time unit
        const fullTime = `${part} ${parts[idx + 1] || ""}`
        const timer = extractTimer(fullTime)
        if (timer) {
          return (
            <span key={idx} className="text-green-600 font-semibold cursor-pointer hover:underline">
              {fullTime}
            </span>
          )
        }
      }
      return part
    })
  }

  // Timer effect
  useEffect(() => {
    let interval: NodeJS.Timeout
    if (timerActive !== null && timerSeconds > 0) {
      interval = setInterval(() => {
        setTimerSeconds((prev) => {
          if (prev <= 1) {
            setTimerActive(null)
            return 0
          }
          return prev - 1
        })
      }, 1000)
    }
    return () => clearInterval(interval)
  }, [timerActive, timerSeconds])

  // Get ingredients for current step
  const getStepIngredients = (step: string): string[] => {
    // Extract ingredient keywords from the step text
    return recipe.ingredients.filter((ing) => {
      const ingName = ing.split(",")[0].toLowerCase()
      return step.toLowerCase().includes(ingName)
    })
  }

  useEffect(() => {
    if (activeTab !== "directions" || !directionsContainerRef.current) return

    const handleScroll = () => {
      const container = directionsContainerRef.current
      if (!container) return

      // Find which step is most visible in the viewport
      let closestStepIndex = 0
      let closestDistance = Number.POSITIVE_INFINITY

      stepRefsRef.current.forEach((ref, idx) => {
        if (!ref) return
        const rect = ref.getBoundingClientRect()
        const containerRect = container.getBoundingClientRect()

        // Calculate distance from step to center of viewport
        const stepCenter = rect.top + rect.height / 2
        const viewportCenter = containerRect.height / 2
        const distance = Math.abs(stepCenter - viewportCenter)

        if (distance < closestDistance) {
          closestDistance = distance
          closestStepIndex = idx
        }
      })

      setCurrentStepIndex(closestStepIndex)
    }

    const container = directionsContainerRef.current
    container?.addEventListener("scroll", handleScroll)
    return () => container?.removeEventListener("scroll", handleScroll)
  }, [activeTab])

  const currentInstruction = recipe.instructions[currentStepIndex]
  const stepIngredients = getStepIngredients(currentInstruction)

  return (
    <div className="min-h-screen bg-background flex flex-col">
      {/* Header */}
      <div className="sticky top-0 bg-background border-b border-border px-4 py-4 sm:px-6 flex items-center justify-between z-10">
        <button onClick={onBack} className="text-2xl text-foreground hover:text-muted-foreground transition-colors">
          ‚Üê
        </button>
        <h1 className="text-2xl sm:text-3xl font-bold text-center text-primary">{recipe.recipe_name}</h1>
        <div className="w-8" />
      </div>

      {/* Tabs */}
      <div className="sticky top-16 bg-background border-b border-border px-4 sm:px-6 py-3 z-10">
        <div className="flex gap-4 max-w-4xl mx-auto">
          <button
            onClick={() => setActiveTab("ingredients")}
            className={`px-6 py-2 rounded-full font-semibold transition-all ${
              activeTab === "ingredients" ? "bg-muted text-foreground" : "text-muted-foreground hover:text-foreground"
            }`}
          >
            Ingredients
          </button>
          <button
            onClick={() => setActiveTab("directions")}
            className={`px-6 py-2 rounded-full font-semibold transition-all ${
              activeTab === "directions" ? "bg-white text-foreground" : "text-muted-foreground hover:text-foreground"
            }`}
          >
            Directions
          </button>
        </div>
      </div>

      {/* Content */}
      <div className="flex-1 overflow-hidden">
        {activeTab === "ingredients" ? (
          <div className="px-4 sm:px-6 py-8 max-w-4xl mx-auto">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-2xl font-bold text-foreground">Get Ingredients</h2>
              <button
                onClick={() => {
                  setCheckedIngredients(Object.fromEntries(recipe.ingredients.map((_, i) => [i, true])))
                }}
                className="text-sm text-primary hover:underline font-semibold"
              >
                Select all
              </button>
            </div>

            <div className="space-y-3">
              {recipe.ingredients.map((ingredient, idx) => (
                <div key={idx} className="flex items-center gap-4 p-3 hover:bg-muted rounded-lg transition-colors">
                  <input
                    type="checkbox"
                    checked={checkedIngredients[idx] || false}
                    onChange={(e) => {
                      setCheckedIngredients((prev) => ({
                        ...prev,
                        [idx]: e.target.checked,
                      }))
                    }}
                    className="w-6 h-6 rounded border-2 border-muted-foreground cursor-pointer"
                  />
                  <span
                    className={`flex-1 text-lg ${
                      checkedIngredients[idx] ? "line-through text-muted-foreground" : "text-foreground"
                    }`}
                  >
                    {ingredient}
                  </span>
                </div>
              ))}
            </div>
          </div>
        ) : (
          <div ref={directionsContainerRef} className="h-full overflow-y-scroll px-4 sm:px-6 py-8 max-w-4xl mx-auto">
            <div className="space-y-12 pb-12">
              {recipe.instructions.map((instruction, idx) => (
                <div
                  key={idx}
                  ref={(el) => {
                    stepRefsRef.current[idx] = el
                  }}
                  className={`transition-opacity ${
                    Math.abs(idx - currentStepIndex) <= 1 ? "opacity-100" : "opacity-50"
                  }`}
                >
                  {/* Step number and ingredients */}
                  <div className="mb-4">
                    <div className="flex items-center gap-2 mb-2">
                      <span className="text-sm font-semibold text-foreground">Step {idx + 1}</span>
                      <span className="text-sm">‚Üó</span>
                    </div>

                    {/* Step ingredients */}
                    {getStepIngredients(instruction).length > 0 && (
                      <div className="flex flex-wrap gap-2 mb-4">
                        {getStepIngredients(instruction).map((ing, i) => (
                          <span
                            key={i}
                            className="px-3 py-1 bg-muted rounded-full text-xs sm:text-sm text-muted-foreground"
                          >
                            {ing.split(",")[0]}
                          </span>
                        ))}
                      </div>
                    )}

                    <div className="h-px bg-border mb-4" />
                  </div>

                  {/* Instruction text with timer highlighting */}
                  <p className="text-2xl sm:text-4xl leading-relaxed font-medium text-foreground mb-8">
                    {instruction.split(/(\d+\s*(?:minutes?|mins?))/gi).map((part, i) => {
                      if (part?.match(/\d+\s*(?:minutes?|mins?)/i)) {
                        const timerVal = extractTimer(part)
                        return (
                          <button
                            key={i}
                            onClick={() => {
                              if (timerVal) {
                                setTimerActive(idx)
                                setTimerSeconds(timerVal)
                              }
                            }}
                            className="text-green-600 font-bold hover:underline cursor-pointer mx-1"
                          >
                            {part}
                          </button>
                        )
                      }
                      return part
                    })}
                  </p>

                  {/* Bottom divider for next step preview */}
                  {idx < recipe.instructions.length - 1 && (
                    <div className="bg-muted rounded-lg p-4 opacity-50">
                      <p className="text-sm font-semibold text-foreground mb-2">Step {idx + 2}</p>
                      <p className="text-sm text-muted-foreground line-clamp-2">{recipe.instructions[idx + 1]}</p>
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>
        )}
      </div>

      {/* Floating progress indicator that only shows on directions tab */}
      {activeTab === "directions" && (
        <div className="fixed bottom-6 left-1/2 transform -translate-x-1/2 z-40">
          <div className="bg-white rounded-full shadow-lg p-3 flex items-center gap-2">
            {recipe.instructions.map((_, idx) => (
              <div
                key={idx}
                className={`h-3 rounded-full transition-all duration-300 ${
                  idx <= currentStepIndex ? "w-5 bg-primary" : "w-3 bg-muted"
                }`}
              />
            ))}
          </div>
        </div>
      )}

      {/* Timer Modal */}
      {timerActive !== null && timerSeconds > 0 && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
          <div className="bg-white rounded-3xl p-8 text-center shadow-lg">
            <h3 className="text-2xl font-bold text-foreground mb-6">Cooking Timer</h3>
            <div className="text-7xl font-bold text-primary mb-8 font-mono">{formatTime(timerSeconds)}</div>
            <div className="flex gap-4">
              <button
                onClick={() => {
                  setTimerActive(null)
                  setTimerSeconds(0)
                }}
                className="flex-1 px-6 py-3 bg-muted text-foreground rounded-full font-semibold hover:bg-muted/80 transition-colors"
              >
                Stop
              </button>
              <button
                onClick={() => setTimerSeconds(0)}
                className="flex-1 px-6 py-3 bg-primary text-primary-foreground rounded-full font-semibold hover:bg-primary/90 transition-colors"
              >
                Done
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
