"use client"

import { useState } from "react"

interface RecipeOverviewProps {
  recipe: {
    recipe_name: string
    summary: string
    prep_time: string
    servings: string
    image?: string
    nutritional_info?: {
      calories: string
      fat?: string
      carbs?: string
      protein?: string
    }
    tools?: string[]
    allergens?: string[]
  }
  onStartCooking: () => void
}

export function RecipeOverview({ recipe, onStartCooking }: RecipeOverviewProps) {
  const [currentServings, setCurrentServings] = useState(Number.parseInt(recipe.servings) || 4)

  const decreaseServings = () => {
    setCurrentServings(Math.max(1, currentServings - 1))
  }

  const increaseServings = () => {
    setCurrentServings(currentServings + 1)
  }

  return (
    <div className="min-h-screen bg-background">
      {/* Hero Image */}
      <div className="w-full aspect-video bg-muted rounded-2xl overflow-hidden mb-8 shadow-md">
        <img
          src={recipe.image || "/placeholder.svg?height=400&width=800&query=food+recipe"}
          alt={recipe.recipe_name}
          className="w-full h-full object-cover"
        />
      </div>

      {/* Main Content Container */}
      <div className="mx-auto max-w-4xl px-4 sm:px-6 lg:px-8 pb-8">
        {/* Title and Link */}
        <div className="mb-6">
          <div className="flex items-center gap-2 mb-2">
            <h1 className="text-4xl sm:text-5xl font-bold text-foreground">{recipe.recipe_name}</h1>
            <button className="text-2xl text-muted-foreground hover:text-foreground transition-colors">üîó</button>
          </div>
        </div>

        {/* Description */}
        <p className="text-base sm:text-lg text-muted-foreground leading-relaxed mb-8">{recipe.summary}</p>

        {/* Stats Cards */}
        <div className="grid grid-cols-2 sm:grid-cols-4 gap-3 mb-8">
          <div className="bg-muted px-4 py-4 rounded-lg">
            <p className="text-sm text-muted-foreground mb-1">Cooking time</p>
            <p className="font-semibold text-foreground text-lg">{recipe.prep_time}</p>
          </div>
          <div className="bg-muted px-4 py-4 rounded-lg">
            <p className="text-sm text-muted-foreground mb-1">Servings</p>
            <p className="font-semibold text-foreground text-lg">{recipe.servings}</p>
          </div>
          {recipe.nutritional_info?.calories && (
            <div className="bg-muted px-4 py-4 rounded-lg">
              <p className="text-sm text-muted-foreground mb-1">Calories</p>
              <p className="font-semibold text-foreground text-lg">{recipe.nutritional_info.calories}</p>
            </div>
          )}
          {recipe.nutritional_info?.protein && (
            <div className="bg-muted px-4 py-4 rounded-lg">
              <p className="text-sm text-muted-foreground mb-1">Protein</p>
              <p className="font-semibold text-foreground text-lg">{recipe.nutritional_info.protein}</p>
            </div>
          )}
        </div>

        {/* Serving Adjuster */}
        <div className="bg-muted px-6 py-6 rounded-lg mb-8 flex items-center justify-between">
          <button
            onClick={decreaseServings}
            className="w-12 h-12 rounded-full bg-background hover:bg-background/80 flex items-center justify-center text-xl font-semibold transition-colors"
          >
            ‚àí
          </button>
          <span className="text-xl font-semibold text-foreground">Cooking for {currentServings}</span>
          <button
            onClick={increaseServings}
            className="w-12 h-12 rounded-full bg-background hover:bg-background/80 flex items-center justify-center text-xl font-semibold transition-colors"
          >
            +
          </button>
        </div>

        {/* Things to Note Section */}
        <div className="mb-8">
          <h2 className="text-2xl font-bold text-foreground mb-6">Things to note</h2>

          {/* Tools */}
          {recipe.tools && recipe.tools.length > 0 && (
            <div className="mb-6">
              <h3 className="font-semibold text-foreground mb-3">Tools</h3>
              <div className="flex flex-wrap gap-2">
                {recipe.tools.map((tool) => (
                  <span
                    key={tool}
                    className="px-4 py-2 bg-muted rounded-full text-sm text-foreground font-medium hover:bg-muted/80 transition-colors"
                  >
                    {tool}
                  </span>
                ))}
              </div>
            </div>
          )}

          {/* Allergens */}
          {recipe.allergens && recipe.allergens.length > 0 && (
            <div>
              <div className="flex items-center gap-2 mb-3">
                <span className="text-xl">‚ö†Ô∏è</span>
                <h3 className="font-semibold text-foreground">Potential Allergens</h3>
              </div>
              <div className="flex flex-wrap gap-2">
                {recipe.allergens.map((allergen) => (
                  <span
                    key={allergen}
                    className="px-4 py-2 bg-yellow-100 text-yellow-800 rounded-full text-sm font-medium"
                  >
                    {allergen}
                  </span>
                ))}
              </div>
            </div>
          )}
        </div>

        {/* Start Cooking Button */}
        <button
          onClick={onStartCooking}
          className="w-full bg-primary hover:bg-primary/90 text-primary-foreground font-semibold py-4 px-6 rounded-full transition-colors text-lg"
        >
          Start Cooking
        </button>
      </div>
    </div>
  )
}
