"use client"

import type React from "react"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { getTimeBasedGreeting } from "@/lib/time-greeting"

interface RecipeInputProps {
  onGenerate: (input: string, inputType: "youtube" | "text") => Promise<void>
  isLoading: boolean
}

export function RecipeInput({ onGenerate, isLoading }: RecipeInputProps) {
  const [youtubeUrl, setYoutubeUrl] = useState("")
  const [recipeText, setRecipeText] = useState("")
  const [inputType, setInputType] = useState<"youtube" | "text">("youtube")
  const [error, setError] = useState("")
  const [greeting, setGreeting] = useState("")
  const [placeholder, setPlaceholder] = useState("")

  useEffect(() => {
    const { greeting: g, placeholder: p } = getTimeBasedGreeting()
    setGreeting(g)
    setPlaceholder(p)

    const interval = setInterval(() => {
      const { greeting: newG, placeholder: newP } = getTimeBasedGreeting()
      setGreeting(newG)
      setPlaceholder(newP)
    }, 60000)

    return () => clearInterval(interval)
  }, [])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError("")

    const input = inputType === "youtube" ? youtubeUrl : recipeText

    if (!input.trim()) {
      setError("Please enter a YouTube URL or recipe text")
      return
    }

    try {
      await onGenerate(input, inputType)
    } catch (err) {
      setError("Failed to generate recipe. Please try again.")
    }
  }

  return (
    <div className="min-h-screen flex flex-col items-center justify-center px-4 py-8">
      <div className="w-full max-w-2xl space-y-8">
        {/* Hero greeting - Large and prominent */}
        <div className="text-center">
          <h1 className="text-5xl sm:text-6xl font-bold text-foreground text-balance leading-tight">{greeting}</h1>
        </div>

        {/* Input form */}
        <form onSubmit={handleSubmit} className="space-y-6">
          <Tabs
            value={inputType}
            onValueChange={(v) => {
              setInputType(v as "youtube" | "text")
              setError("")
            }}
            className="w-full"
          >
            <TabsList className="grid w-full grid-cols-2 h-10">
              <TabsTrigger value="youtube" className="text-base">
                YouTube
              </TabsTrigger>
              <TabsTrigger value="text" className="text-base">
                Text
              </TabsTrigger>
            </TabsList>

            {/* YouTube Tab */}
            <TabsContent value="youtube" className="space-y-4 mt-6">
              <textarea
                placeholder="https://youtube.com/watch?v=..."
                value={youtubeUrl}
                onChange={(e) => setYoutubeUrl(e.target.value)}
                className="w-full px-6 py-6 text-2xl border-2 border-input bg-background text-foreground rounded-2xl focus:outline-none focus:ring-2 focus:ring-primary focus:border-transparent disabled:opacity-50 placeholder-gray-400"
                disabled={isLoading}
              />
              <p className="text-sm text-muted-foreground px-2">
                The video must have captions or auto-generated subtitles.
              </p>
            </TabsContent>

            {/* Text Tab - Large textarea with big fonts */}
            <TabsContent value="text" className="space-y-4 mt-6">
              <textarea
                placeholder={placeholder}
                value={recipeText}
                onChange={(e) => setRecipeText(e.target.value)}
                className="w-full h-64 px-6 py-6 text-2xl border-2 border-input bg-background text-foreground rounded-2xl focus:outline-none focus:ring-2 focus:ring-primary focus:border-transparent disabled:opacity-50 resize-none placeholder-gray-400"
                disabled={isLoading}
              />
              <p className="text-sm text-muted-foreground px-2">Provide at least 50 characters for best results.</p>
            </TabsContent>
          </Tabs>

          {/* Error message */}
          {error && (
            <div className="flex items-center gap-3 p-4 bg-red-50 dark:bg-red-950 border border-red-200 dark:border-red-800 rounded-lg">
              <span className="text-red-600 dark:text-red-400 text-lg">‚ö†</span>
              <p className="text-sm text-red-700 dark:text-red-300">{error}</p>
            </div>
          )}

          {/* Submit button */}
          <Button
            type="submit"
            size="lg"
            className="w-full h-16 text-lg font-semibold rounded-full"
            disabled={isLoading}
          >
            {isLoading ? "Generating Recipe..." : "Create Recipe"}
          </Button>
        </form>
      </div>
    </div>
  )
}
